}
.single-post .featured-image {
  margin-bottom: 2em;
}
.single-post .post-navigation,
.single-post #comments {
  float: left;
  display: block;
  margin-right: 2.1276595745%;
  width: 65.9574468085%;
  margin-left: 17.0212765957%;
  margin-right: 0;
  content: "";
  display: table;
}
.single-post .post-navigation:last-child,
.single-post #comments:last-child {
  margin-right: 0;
}
.single-post .post-navigation {
  margin-top: 2em;
}
@media screen and (min-width: 930px) {
  .single-post main > article .text-block,
.single-post main > article .featured-image,
.single-post main > article .social-share,
.single-post main > article footer {
    float: left;
    display: block;
    margin-right: 2.1276595745%;
    width: 65.9574468085%;
    margin-left: 17.0212765957%;
    margin-right: 0;
    content: "";
    display: table;
  }
  .single-post main > article .text-block:last-child,
.single-post main > article .featured-image:last-child,
.single-post main > article .social-share:last-child,
.single-post main > article footer:last-child {
    margin-right: 0;
  }
}
.single-post main > article .image-block {
  display: table;
  float: left;
  display: block;
  margin-right: 2.1276595745%;
  width: 100%;
}
.single-post main > article .image-block:last-child {
  margin-right: 0;
}
.single-post main > article .image-block img {
  width: 100%;
}
.single-post main > article .image-block .text {
  display: table-cell;
  vertical-align: center;
}
.single-post main > article .entry-title,
.single-post main > article .byline {
  text-align: center;
}
.single-post header .byline {
  display: block;
}
.single-post table td {
  border: 1px solid;
  padding: 0.5em;
  background: rgba(255, 255, 255, 0.63);
}

/*
Article Footer
*/
footer.entry-footer {
  background: white;
  padding: 1em;
  border: 1px solid #D2D2D2;
}
footer.entry-footer span {
  display: inline-block;
  margin-right: 1em;
}
footer.entry-footer span:last-child {
  margin-right: 0;
}

/*
ACO Home
*/
.page section.aco-home {
  padding: 4.5em 0 4.5em;
  overflow: hidden;
}
.page section.aco-home#intro {
  position: relative;
  background-position: 50% 50%;
  padding: 6em 0 4em;
}
.page section.aco-home#intro:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  content: "";
  background-image: linear-gradient(119deg, rgba(0, 0, 0, 0.85), rgba(0, 0, 0, 0.06) 126%);
}
.page section.aco-home#intro div,
.page section.aco-home#intro a,
.page section.aco-home#intro p {
  z-index: 999;
  position: relative;
}
.page section.aco-home#intro h1 {
  font-size: 1.75em;
}
.page section.aco-home#intro h1,
.page section.aco-home#intro p {
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}
.page section.aco-home#video .sub-section0 {
  padding-top: 3em;
}
.page section.aco-home#video .boxed {
  background: rgba(29, 17, 0, 0.54);
}
.page section.aco-home#video blockquote {
  font-size: 1em;
}

#content section.row:nth-child(even) ._form_30 ._submit {
  background: #004280 !important;
}

.page.page-template-default article .entry-content {
  z-index: 99;
}

/*--------------------------------------------------------------
10.2 Asides
--------------------------------------------------------------*/
.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
  display: none;
}

aside#left-sidebar {
  margin-top: 2em;
}
aside#left-sidebar ul.menu > li {
  font-weight: 700;
  margin-bottom: 1.25em;
  line-height: 1em;
  font-weight: 400;
  font-size: 0.8em;
}

.widget_nav_menu ul ul {
  margin-left: 1em;
}
.widget_nav_menu ul ul li {
  list-style-type: disc;
}

#secondary li {
  margin-bottom: 0.5em;
}

.sidebar-cta {
  padding: 0.5em 0 0 0;
  text-align: center;
}

aside .textwidget {
  font-size: 0.8em;
}
aside .textwidget h3 {
  font-size: 1.2em;
}

/*--------------------------------------------------------------
10.3 Comments
--------------------------------------------------------------*/
.comment-content a {
  word-wrap: break-word;
}

.bypostauthor {
  display: block;
}

ol.comment-list,
ol.children {
  margin: 0;
}

li.comment {
  list-style: none;
  clear: both;
  background: rgba(255, 255, 255, 0.768627451);
  padding: 1em;
  margin: 0.5em;
  box-shadow: 0px 0px 8px rgba(51, 51, 51, 0.2588235294);
  overflow: hidden;
}
li.comment:first-child {
  margin: 1em 0;
}

.comment-author.vcard img {
  float: left;
  width: 32px;
  height: 32px;
  margin-top: 0.25em;
  margin-right: 1em;
}

.comment-content {
  float: left;
  width: calc(100% - 32px - 1em);
  margin-left: calc(32px + 1em);
}

footer.comment-meta {
  width: 100%;
  display: flex;
  clear: both;
  flex-direction: column-reverse;
}

.comment-metadata {
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
}

#commentform input {
  max-width: 500px;
}

