/*--------------------------------------------------------------
14.0 Printing
--------------------------------------------------------------*/
@media print {
  body {
    color: black;
    background: none;
    font-size: 16px;
  }

  header#masthead {
    background: none;
    box-shadow: none;
  }
  header#masthead:before, header#masthead:after {
    content: none;
  }
  header#masthead .wrap .site-branding {
    width: 100%;
  }
  header#masthead .wrap .site-title a {
    font-size: 24px !important;
    text-indent: 0;
    width: 100%;
    background-image: none !important;
  }

  #site-navigation {
    display: none;
  }

  .secondary-navigation-container {
    display: none;
  }

  .wp-block-ugb-container {
    --ugb-text-color: #000 !important;
    color: #000;
  }

  main#main header.entry-header {
    background: none;
    color: #000;
  }
  main#main .hero #breadcrumbs a {
    color: #000;
  }
  main#main .entry-content {
    margin: 0;
    padding: 0;
    border: none;
    max-width: 6in;
    margin: 0 auto;
  }

  .flickity-viewport {
    overflow: visible !important;
    height: auto !important;
  }
  .flickity-viewport .flickity-slider {
    position: static !important;
    margin: 0 auto;
    transform: none !important;
  }
  .flickity-viewport li.slide-item {
    position: initial !important;
    float: left;
  }

  #respond, .akismet_comment_form_privacy_notice {
    display: none;
  }

  .post-navigation {
    display: none !important;
  }

  footer#colophon {
    display: none;
  }

  .entry-content p a:not(.btn):not(.button):not([class*=slide]):not(.salient-tab-label), .entry-content li:not(.menu-item) a:not(.btn):not(.button):not([class*=slide]):not(.salient-tab-label):not(.wp-block-button__link):not(.salient-button) {
    text-decoration: none;
    background-image: linear-gradient(currentColor, currentColor);
    background-size: 100% 1px;
    background-position: center bottom 29%;
    background-repeat: no-repeat;
    padding: 0.75em 0;
  }

  #drift-widget {
    display: none !important;
  }
}
.gdpr-hide {
  display: none;
}